body {
    background: white;
    padding-top: 10rem;
}

.card {
    width: 20rem;
    height: 30rem;
    box-shadow: none;
}

.card a {
    display: none;
}

.card:hover {
    box-shadow: 5px 10px 20px 1 px rgba(0, 0, 0, 0.5) !important;
}

.card-body {
    padding: 3rem 0 !important;
}

.card-text {
    font-size: 0.9rems;
    padding: 0.4rem 1.9rem;
}

.container-fluid row {
    padding-top: 6rem;
}

.overflow {
    overflow: hidden;
}

.card-img-top {
    transform: scale(1);
    transition: transform 0.5s ease;
}

.card-img-top:hover {
    transform: scale(1.8);
}